body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-left: 40px;
  padding-right: 40px;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.container {
    margin-bottom: 20px;
}
.backdrop {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
}
.card {
    margin: 0 auto;
    position: relative;
    display: flex;
    justify-content: center;
    align-self: center;
    box-shadow: 0 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
    width: 70%;
}
.movieData {
    width: 100%;
    padding-top: 5vw;
    position: absolute;
    display: flex;
}
.card-img {
    opacity: 0.4;
    filter: alpha(opacity=30);
width: 100%;}
.figure-img {
    margin-bottom: 0;
    border-radius: 3px;
}
.poster {
    position: absolute;
    padding: 0;
    margin: -150px 0 0 50px;
    box-shadow: 0 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
}
.movie_details {
    text-align: left;
}
.movie-title {
    display: flex;
    align-items: baseline;
}
.card-footer {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

@media only screen and (max-width: 990px){
    .poster{
        position: relative;
        margin: -250px 0 0 0px;
        box-shadow: none;
    }

    .figure-img{
        box-shadow: 0 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
    }

}
@media only screen and (max-width: 768px){

.card{
    margin-top: 30px;
    width: 100%;
}

.poster{
    position: relative;
    margin: -180px 0 0 0px;
    box-shadow: none;
}

.figure-img{
    box-shadow: 0 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
}


}
.searchInput {
    margin: 0 auto;
    width: 30%;
    outline: none;
    padding: 0;
    border: none;
    border-radius: 5px;
    font-size: 20px;
    outline: none;
}
input:focus:active {
    background-color: none;
}
a,
button,
input,
select,
textarea {
    -webkit-tap-highlight-color: #ccc;
}
.jumbotron {
    background-image: url('https://cdn.europosters.eu/image/1300/posters/rocky-cityscape-i6110.jpg');
    background-repeat: none;
    background-size: cover;
    height: 40vh;
    display: flex;
    justify-content: space-between;
    flex-direction: column;


}

.title, .intoduction{
    -webkit-text-shadow: 3px 5px 2px #747474;
    text-shadow: 1px 1px 1px rgb(63, 63, 63), 
    3px 3px 5px rgb(116, 116, 116); ;
    
}

.intoduction{
    color: #eeeeee;
}
@media only screen and (max-width: 720px){
    .searchInput{
        width: 100%;
    }
}
@media only screen and (max-width: 375px){
    .jumbotron {
        margin-bottom: 0;
    }

    .title, .intoduction{
        padding: 0;
        margin: 0 auto;
    }

    .searchInput{
        width: 100%;
    }
}
